/* Глобальні стилі */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    overflow: hidden;
    background-color: #000;
}

@media (max-width: 768px) {

    #player,
    .enemy,
    .item {
        width: 32px;
        height: 32px;
    }
}

#gameCanvas {
    width: 100%;
    height: 100%;
    display: block;
}

#player,
.enemy,
.item {
    image-rendering: pixelated;
}

#game-container {
    position: relative;
    width: 100vw;
    height: 100vh;
    background-color: #87CEEB;
    overflow: hidden;
    background-image: url('assets/sprites.webp');
    background-size: 1024px 1024px;
    /* Розмір спрайт-листа */
    background-position: 0 0;
    /* Початкова позиція фону */
    background-repeat: repeat;
    /* Повторення для заповнення */
}

/* Гравець */
#player {
    position: absolute;
    width: 64px;
    height: 64px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url('assets/sprites.webp');
    background-size: 1024px 1024px;
    /* Розмір спрайт-листа */
    background-position: -470px -370px;
    /* Виправлена позиція персонажа у спрайт-листі */
    z-index: 10;
    /* Пріоритет гравця над іншими елементами */
}

/* Противники */
.enemy {
    position: absolute;
    width: 64px;
    height: 64px;
    background-image: url('assets/sprites.webp');
    background-size: 1024px 1024px;
    background-position: -64px -128px;
    /* Позиція спрайта противника */
}

/* Предмети */
.item {
    position: absolute;
    width: 32px;
    height: 32px;
    background-image: url('assets/sprites.webp');
    background-size: 1024px 1024px;
    background-position: -128px -128px;
    /* Позиція спрайта предмета */
}

/* Інтерфейс користувача */
#ui {
    position: absolute;
    top: 10px;
    left: 10px;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 10px;
    border-radius: 5px;
    font-size: 14px;
    /* Оптимізований розмір шрифту */
    z-index: 20;
    /* Завжди поверх гри */
}

#ui {
    position: absolute;
    top: 20px;
    left: 20px;
    background: rgba(0, 0, 0, 0.7);
    padding: 10px;
    color: white;
    font-family: 'Press Start 2P', cursive;
    font-size: 14px;
    z-index: 15;
}

.middle {
    display: flex;
    gap: 10px;
}

/* Джойстик */
#joystick {
    position: absolute;
    bottom: 50px;
    left: 50px;
    width: 120px;
    height: 120px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 15;
}

#joystick .control-stick {
    width: 60px;
    height: 60px;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    position: absolute;
    cursor: pointer;
    transition: all 0.1s ease;
}

#joystick .control-stick:active {
    background: rgba(200, 200, 200, 1);
}